import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthorizeModificationComponent } from './authorize-modification/authorize-modification.component';

const routes: Routes = [
  {
    path:"",
    component:AuthorizeModificationComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AuthorizeModificationRoutingModule { }
